mplificadas para representar a una comunidad variada en términos de género, edad y capacidades físicas. La textura visual es completamente lisa y limpia, típica de ilustraciones modernas. El tono general transmite unidad, inclusión y una energía positiva de empoderamiento colectivo"}})])])])])])
