},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_c('div',{staticClass:"col-lg-9 order-lg-1 order-2",attrs:{"data-aos":"zoom-in"}},[_c('p',[_vm._v("En muchos espacios aún es necesario recordar que las personas con discapacidad son, ante todo, personas. El modelo social define la discapacidad como una construcción social, cambiante y contextual, que responde a formas de pensamiento y estructuras históricas.")]),_c('div',{staticClass:"bloque-texto-c color-secundario p-3",staticStyle:{"background-color":"#F8EFEA"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Este modelo identifica dos elementos fundamentales: las deficiencias y las barreras. Son estas últimas las que generan la discapacidad; es decir, no es la persona quien “posee” una discapacidad, sino que es el entorno el que la produce. En este sentido, términos como “discapacitado”, “persona en situación de discapacidad” o “persona en condición de discapacidad” deben ser evitados, ya que colocan la discapacidad en la persona y no en el entorno. Además, expresiones como “persona en situación” sugieren una condición transitoria que se espera superar, lo cual no corresponde con la realidad de muchas personas.")])])]),_c('div',{staticClass:"col-lg-3 col-10 order-lg-2 order-1 mb-lg-0 mb-4",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{staticClass:"img-fluid",attrs:{"src":require("@/assets/curso/temas/tema2/img-17.png"),"alt":"Imagen decorativa"}})])])])
