},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bloque-texto-g__texto p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"titulo2 mb-4",attrs:{"data-aos":"fade-left"}},[_c('h5',{staticClass:"mb-0"},[_vm._v("La primera es el derecho a la accesibilidad")])]),_c('br'),_c('p',{staticClass:"mb-0"},[_vm._v("Este derecho está consagrado en el artículo 9 de la Convención sobre los Derechos de las Personas con Discapacidad (2006), ratificada por Colombia, mediante la Ley 1346 de 2009 y en el artículo 14 de la Ley 1618 de 2013. La Convención reconoce la accesibilidad como un derecho humano, mientras que la Ley Estatutaria 1618 de 2013 —“Por medio de la cual se establecen las disposiciones para garantizar el pleno ejercicio de los derechos de las personas con discapacidad”— la define como un derecho fundamental, inherente a esta población.")])])
