},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-10 col-12"},[_c('p',[_vm._v("Existen múltiples ejemplos de ajustes razonables, que varían según las necesidades específicas de cada persona y tipo de discapacidad. No hay soluciones universales. Cada persona puede elegir qué apoyos, tecnologías o estrategias se ajustan mejor a sus necesidades. Por ello, todo ajuste razonable debe ser concertado directamente con la persona con discapacidad, respetando su voz, sus preferencias y su derecho a decidir. ")])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido mb-3 mt-2"},[_c('h5',[_vm._v("Figura 6.")]),_c('span',[_c('em',[_vm._v("Dudas sobre accesibilidad")])])]),_c('div',{staticClass:"row bg-color-5 justify-content-center align-items-center p-4"},[_c('div',{staticClass:"col-lg-10"},[_c('figure',{staticClass:"desktop"},[_c('img',{staticClass:"m-auto",staticStyle:{"width":"1043px"},attrs:{"src":require("@/assets/curso/temas/tema2/img-49.svg"),"alt":"La ilustración presenta a una persona pensativa rodeada de burbujas de diálogo con preguntas relacionadas sobre la accesibilidad para personas con discapacidad. La escena se compone de una figura central, una persona con cabello rojizo y vestimenta verde, situada en el centro de la imagen. Alrededor de ella, se dispersan dos burbujas de diálogo que contienen preguntas relacionadas con la manera en cómo la accesibilidad se puede dar para personas con discapacidad. La composición está equilibrada, guiando la vista del espectador a través de las preguntas y hacia la persona central, quien parece estar reflexionando sobre ellas, señalada por los signos de interrogación sobre su cabeza. La disposición de las burbujas de diálogo y su tamaño varían, creando un ritmo visual y enfatizando la importancia de cada cuestión. El sujeto principal es una persona joven, con cabello rojizo largo y una expresión de preocupación, con las manos entrelazadas bajo la barbilla. La vestimenta de la persona consiste en una blusa verde oscura. Los signos de interrogación sobre su cabeza indican incertidumbre o reflexión. El estilo de la imagen es simple y directo, con líneas limpias y colores planos, lo que sugiere una ilustración didáctica o explicativa. La técnica empleada parece ser digital, con contornos definidos y un diseño gráfico. El uso de burbujas de diálogo es característico de las ilustraciones que buscan comunicar información de manera clara y concisa. La paleta de colores es limitada, con tonos suaves y agradables, lo que contribuye a la legibilidad y facilita la comprensión de la información. El fondo es blanco, lo que centra la atención en la persona y las preguntas. La iluminación es uniforme, sin sombras ni efectos especiales, lo que enfatiza la claridad y la simplicidad de la comunicación visual. La ausencia de elementos decorativos en el fondo refuerza el propósito didáctico de la imagen. El ambiente general es neutro, propicio para la reflexión sobre el tema del apoyo y la inclusión, sin distracciones visuales que desvíen la atención del mensaje principal."}})]),_c('figure',{staticClass:"movil"},[_c('img',{staticClass:"m-auto",staticStyle:{"width":"1043px"},attrs:{"src":require("@/assets/curso/temas/tema2/img-49-2.svg"),"alt":"La ilustración presenta a una persona pensativa rodeada de burbujas de diálogo con preguntas relacionadas sobre la accesibilidad para personas con discapacidad. La escena se compone de una figura central, una persona con cabello rojizo y vestimenta verde, situada en el centro de la imagen. Alrededor de ella, se dispersan dos burbujas de diálogo que contienen preguntas relacionadas con la manera en cómo la accesibilidad se puede dar para personas con discapacidad. La composición está equilibrada, guiando la vista del espectador a través de las preguntas y hacia la persona central, quien parece estar reflexionando sobre ellas, señalada por los signos de interrogación sobre su cabeza. La disposición de las burbujas de diálogo y su tamaño varían, creando un ritmo visual y enfatizando la importancia de cada cuestión. El sujeto principal es una persona joven, con cabello rojizo largo y una expresión de preocupación, con las manos entrelazadas bajo la barbilla. La vestimenta de la persona consiste en una blusa verde oscura. Los signos de interrogación sobre su cabeza indican incertidumbre o reflexi