},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"tarjeta--BG03 p-5"},[_c('div',{staticClass:"row justify-content-center mb-3",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-10 col-12"},[_c('p',[_vm._v("Comprender las tres dimensiones de la accesibilidad —accesibilidad, ajustes razonables y acciones afirmativas— es fundamental para quienes facilitan procesos de valoración de apoyos. Es necesario tener presente que la accesibilidad constituye un derecho y, por tanto, debe garantizarse mediante estrategias de implementación adaptadas tanto al tipo de discapacidad como a las necesidades particulares de cada persona. Estas estrategias deben construirse siempre de manera concertada con la persona, lo que se denomina ajuste razonable (REDI, 2023).")])])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"bloque-texto-c color-secundario-oscuro"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-3 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img-32.png"),"alt":"Imagen decorativa"}})])]),_c('div',{staticClass:"col-lg-9"},[_c('div',{staticClass:"px-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La acción afirmativa, por su parte, representa la puesta en práctica de dicha estrategia: es la materialización concreta de los ajustes razonables previamente definidos.")])])])])])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-10 col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("En síntesis, estas tres dimensiones tienen un propósito común y esencial: garantizar el derecho de todas las personas con discapacidad a vivir con libertad, autonomía y plena participación en la sociedad.")])])])])
