},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center mb-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-10 col-12"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido mb-3 mt-2"},[_c('h5',[_vm._v("Figura 8.")]),_c('span',[_c('em',[_vm._v("Representación enfoque y capacitismo")])])]),_c('div',{staticClass:"row bg-color-5 justify-content-center align-items-center p-4"},[_c('div',{staticClass:"col-lg-10"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"1043px"},attrs:{"src":require("@/assets/curso/temas/tema2/img-48.svg"),"alt":"La imagen muestra una escena al aire libre en un campo de fútbol rodeado de césped verde. Es un día soleado con un cielo azul claro adornado por algunas nubes blancas dispersas. En el centro de la imagen, hay cuatro personas jugando al fútbol, tres hombres y una mujer, todos con prótesis en las piernas que les permiten moverse y participar activamente en el juego. La mujer, situada hacia la derecha y vestida con una camiseta amarilla y pantalones cortos azules, está corriendo con energía hacia el balón blanco y negro que se encuentra cerca de sus pies. Los otros tres jugadores, vestidos con combinaciones de ropa deportiva con tonalidades púrpura y azul, están también en acción persiguiendo el balón, cada uno mostrando una postura dinámica y concentrada. Dos de los hombres tienen prótesis curvas que son características de los diseños para deportes, mientras que el otro usa una prótesis similar que le permite también participar activamente. Detrás de los jugadores, se puede ver una línea de arbustos verdes que marca el límite del campo de juego, añadiendo un elemento natural al fondo. Los movimientos de las personas sugieren competitividad y entusiasmo, creando un ambiente lleno de energía, cooperación y esfuerzo. La ilustración transmite una sensación positiva, promoviendo la inclusión y mostrando la determinación de los participantes. El entorno general es claro y vibrante, destacando un momento de actividad recreativa y deportiva"}})])])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row no-gutters align-items-center mb-5 style___box_1 m-0",attrs:{"data-aos":"zoom-in"}},[_c('div',{staticClass:"col-xl-auto px-0"},[_c('figure',{staticClass:"d-none d-xl-block"},[_c('img',{staticClass:"img-hero m-auto",attrs:{"src":require("@/assets/curso/temas/tema2/img-34.png"),"alt":""}})])]),_c('div',{staticClass:"col-xl px-0"},[_c('div',{staticClass:"d-flex align-items-start"},[_c('img',{staticClass:"icon-inline d-none d-lg-block me-3",attrs:{"src":require("@/assets/curso/temas/tema2/img-35.svg"),"alt":"Imagen decorativa"}})]),_c('p',{staticClass:"mb-0"},[_vm._v("En este punto, es fundamental insistir en que la valoración de apoyos no debe realizarse desde el enfoque de la deficiencia, sino que se propone un cambio de perspectiva centrado en la autonomía de las personas con discapacidad. Este enfoque implica comprender a cada persona, dentro del proceso de valoración, desde su capacidad de autodeterminación, su derecho a la accesibilidad comunicativa y la necesidad de eliminar barreras actitudinales, físicas y comunicativas. Esta mirada favorece la promoción del modelo de vida independiente y de vida en comunidad, así como la incorporación del enfoque biopsicosocial propuesto por la Clasificación Internacional del Funcionamiento, de la Discapacidad y de la Salud (CIF).")])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"bloque-texto-c color-primario-oscuro p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Otro aspecto que puede complementar el lineamiento actual es el reconocimiento del derecho al error. Históricamente, a las personas con discapacidad —y en particular a aquellas con discapacidad intelectual— se les ha limitado la posibilidad de tomar decisiones, bajo el argumento de protegerlas de sus propias elecciones, por considerar que podrían equivocarse. Sin embargo, tanto la Convención sobre los Derechos de las Personas con Discapacidad de Naciones Unidas (2006), como la Ley 1346 de 2009 —que aprueba dicha Convención— y la Ley 1996 de 2019 —que establece el régimen para el ejercicio de la capacidad legal de las personas con discapacidad mayores de edad— reconocen explícitamente el derecho a equivocarse.")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('p',[_vm._v("Errar es parte de la experiencia humana. Exigir que las decisiones de una persona con discapacidad sean “perfectas” implica imponerle una carga injusta. Esta expectativa se conoce como "),_c('b',[_vm._v("capacitismo")]),_vm._v(", un concepto que señala cómo las personas con discapacidad son evaluadas bajo estándares más exigentes, esperando que sus decisiones se asemejen —o incluso superen— las que tomaría una persona considerada “inteligente” o “plenamente capaz”. Equivocarse no disminuye la capacidad de nadie. Las personas con discapacidad, como cualquier otra, tienen derecho a decidir y a equivocarse, sin que ello afecte su dignidad ni su autonomía. (Álvarez, 2023).")])
